@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

*:focus {
  outline: 0 !important;
}

html {
  font-size: 62.5%; /*1rem = 10px*/
  box-sizing: border-box;
  height: 100%;
  position: relative;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.4rem;
  background-color: rgb(233, 239, 240);
  margin: 0;
  height: 100vh;
  width: 100vw;
  min-height: 100%;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

a {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

a:focus {
  -webkit-tap-highlight-color: transparent;
}

.container {
  max-width: inherit;
  margin: 30px auto;
  overflow: auto;
  min-height: inherit;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  margin: 0px 0px 30px 0px;
  width: inherit;
  height: 65px;
  padding: 10px 10px 10px 30px;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-around;
}

.nav-links {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  list-style: none;
}

/* .nav-links .selected .link-1 {
  box-shadow: inset 7px 7px 13px #e6e6e6, inset -7px -7px 13px #f4f4f4;
} */

.lnk:hover {
  box-shadow: inset 7px 7px 13px #e6e6e6, inset -7px -7px 13px #f4f4f4;
}

.selected .lnk {
  box-shadow: inset 7px 7px 13px #e6e6e6, inset -7px -7px 13px #f4f4f4;
}

.selected .link-1 {
  box-shadow: inset 7px 7px 13px #e6e6e6, inset -7px -7px 13px #f4f4f4;
}

a {
  text-decoration: none;
}

.link-1 {
  padding: 12px 24px;
  border-radius: 24px;
  /* background: #d6dddd;
  box-shadow: 4px 5px 8px #bdbde8, 6px 8px 7px #efeff1; */
  background: rgb(233 239 240);
  /* box-shadow: 5px 5px 13px #e6e6e6, -5px -5px 13px #f4f4f4; */
  box-shadow: 5px 5px 10px #dbe1e2, -5px -5px 10px #f7fdfe;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease-in;
}

.link-1:hover {
  box-shadow: inset 5px 5px 10px #dbe1e2, inset -5px -5px 10px #f7fdfe;
  transform: scale(1.05, 1.05);
}

.profile-links {
  display: flex;
  /* margin: 10px 0px; */
  padding: 15px 0px;
  right: 1vw;
  top: 1.45vh;
  position: fixed;
}

.prnts-logo {
  transition-property: all;
  transition-duration: 0.7s;
  transition-timing-function: ease-in;
}

.prnts-logo:hover {
  transform: scale(1.1, 1.1);
}

.link-2 {
  margin: 0px 10px;
  padding: 12px 16px;

  /* background: rgb(0, 0, 0); */
  background: #e9eff0;
  border-radius: 20px;
  box-shadow: inset 5px 5px 10px #dbe1e2, inset -5px -5px 10px #f7fdfe;
  /* background: #1c1c1c; */
  /* box-shadow: 9px 9px 6px #1b1b1b, -9px -9px 6px #1d1d1d; */
  color: rgb(0, 0, 0);
  letter-spacing: 1px;
  /* font-size: smaller; */
  font-weight: bolder;
  /* box-shadow: 5px 5px 13px #e6e6e6, -5px -5px 13px #f4f4f4; */
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease-in;
}

.link-2:hover {
  /* color: rgb(198, 198, 198); */
  /* box-shadow: inset 5px 5px 10px #dbe1e2,
            inset -5px -5px 10px #f7fdfe; */
  /* color: white; */
  transform: scale(1.05, 1.05);
}

