/* unvisited link */
a:link {
  color: rgb(0, 0, 0);
}

/* visited link */
a:visited {
  color: rgb(0, 0, 0);
  /* box-shadow: inset 7px 7px 13px #e6e6e6, inset -7px -7px 13px #f4f4f4; */
}

