.btn {
  display: block;
  background: #e9eff0;
  color: #000;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  box-shadow: 5px 5px 10px #dbe1e2, -5px -5px 10px #f7fdfe;
  border-radius: 25px;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
  max-width: fit-content;
  max-height: fit-content;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease-in;
}

.btn:hover {
  box-shadow: inset 5px 5px 10px #dbe1e2, inset -5px -5px 10px #f7fdfe;
  cursor: pointer;
  transform: scale(1.05, 1.05);
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  /* cursor: pointer; */
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}
