/* Name */
.artist-title {
  overflow: auto;
  padding: 18px 0px 0px 0px;
  margin-top: 20px;
  height: 60px;
  background: linear-gradient(
    90deg,
    #1a4bff 15.22%,
    #62d8f9 32.09%,
    #ec5157 48.96%,
    #ff18bd 67.94%,
    #f9d649 85.34%,
    #f08e35 99.57%
  );
  background-clip: inherit;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.user {
  overflow: hidden;
  padding: 0px;
  height: 30px;
  display: flex;
}

.desc {
  overflow: hidden;
  height: 50px;
  display: flex;
}

.user-name {
  background: linear-gradient(
    110.78deg,
    #1a4bff 15.22%,
    #62d8f9 32.09%,
    #ec5157 48.96%,
    #ff18bd 67.94%,
    #f9d649 85.34%,
    #f08e35 99.57%
  );
  background-clip: inherit;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
