.art-c {
  box-sizing: border-box;
  margin: 0px;
  margin-top: 4vh;

  min-width: 0px;
  height: fit-content;
  /* background-color: rgb(202, 202, 202); */
  /* background-color: rgb(230 235 234 / 1); */
  display: flex;
  position: relative;
  flex: 1 1 0%;
  min-height: 5vh;
  align-items: center;
  justify-content: center;
}

.image-c {
  height: fit-content;
  width: fit-content;
  /* background-color: rgb(177, 175, 175); */
  background-color: rgb(233 239 240);
  border-radius: 20px;
  padding: 10px 10px;
  /* display: grid; */
  /* align-items: center; */
  /* justify-items: center; */
  overflow: hidden;
  /* box-shadow: 3px 3px 8px #d9d9d9, -4px -4px 8px #cccccc; */
  box-shadow: 5px 5px 12px #dbe1e2, -5px -5px 12px #f7fdfe;
  display: grid;
  justify-self: center;
  margin: 0px 10px 10px 10px;
  justify-content: center;
  align-content: center;
}
/* transform: translateY(-1.5rem); */

.image-c img {
  width: 500px;
  height: auto;
  max-width: 80vw;
}

