.editions-dropdown {
  display: flex;
  align-items: baseline;
  justify-content: center;
  gap: 10px;
  background: rgb(233 239 240);
  box-shadow: 5px 5px 12px #dbe1e2, -5px -5px 12px #f7fdfe;
  border-radius: 15px;
  margin: 10px 0px;

  padding: 7px 15px;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease-in;
}

.editions-dropdown:hover {
  transform: scale(1.05, 1.05);
  cursor: pointer;
}

.editions-dropdown select {
  font-family: "Poppins";
  font-weight: bolder;
  background: inherit;
  outline: none;
  border: none;
  box-shadow: inset 5px 5px 12px #dbe1e2, inset -5px -5px 12px #f7fdfe;
  border-radius: 20px;
  padding: 7px 10px;
}

.editions-dropdown select:hover {
  /* box-shadow: inset 5px 5px 12px #dbe1e2, inset -5px -5px 12px #f7fdfe; */
  cursor: pointer;
}

